@import "./_variable.scss";
@import "./_mixin.scss";

.NavBarMainContainer{
    position: fixed;
    top: 0;
    @include size("both", 100%, 120px);

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    @include bgc();
    z-index: 2;

}
.HamburgerButtonContainer{
    display: none;
}
.bd{
    border: 1px solid #000;
}
.BannerContainer{
    margin: 10px 0px;
}
.NavBarContainer{
    @include size("height", 0 , 100%);

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.NavBarLink{

    padding: 5px 10px;
    text-decoration: none;
    color: $textColor;
}

/* Home */
.MainSectionContainer{
    padding-top: 200px;
    @include size("height", 0 , calc(100vh));

    display: flex;
    justify-content: center;
    position: relative;
    @include bgc();
}
.IntroContainer{
    @include size("both",1200px, max-content);
    display: flex;
    flex-direction: column;
    height: 90%;
    justify-content: space-between
}
.IntroTop{
    display: flex;
    @include size("width", 100%, 0);
}
.IntroLeft{
    min-width: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

.IntroDesc{
    color: $textColor;
    width: inherit;
    text-align: left;
    font-size: 15px;

}
.IntroAvatar{
    @include size("both",150px, 150px);
    border-radius: 50%;
    border: 1px solid rgba(105, 105, 105, 0.281);
    margin-bottom: 20px;
}
.IntroContactDetail{
    @include size("width", 100%, 0);
    text-align: left;
    white-space: nowrap;
    display: flex;
    align-items: center;
    gap: 10px;
}
.IntroContact{

    margin-top: 50px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
}
.ContactInfoContainer{
    @include size("width", max-content, 0);
    border-bottom: 1px solid rgba(105, 105, 105, 0.281);
    padding-bottom: 20px;
}
.ContactIcon{
    font-size: 30px;
    @include bgc(rgb(22, 22, 22));
    color: #fff;
    padding: 7px;
    border-radius: 50%;
}
.ContactSocial{
    display: flex;
    gap: 10px;
}
 /* Skill */
 .SectionContainer{
    padding-top: 120px;
    display: flex;
    justify-content: center;
    position: relative;
    background-color: #fff;
    @include bgc();
 }
.SectionContainer:hover .lineLeft{
    @include size("width", 100%, 0);
}
 .Container{
     @include size("width", 1200px, 0);
     display: flex;
     align-items: center;
     flex-direction: column;
 }
 .SkillContainer{
     @include size("both", max-content, max-content);
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-top: 50px;
}
.SkillSetLabel{
    font-weight: bold;
    display: flex;
    align-items: center;
    gap: 5px;
}
.SkillSetContainer{
    display: flex;
    flex-direction: column;
    text-align: left;
}
/* Project */
.ProjectListContainer{
    margin-top: 50px;
    @include size("width", 60%, 0);
    text-align: left;
    display: flex;
    flex-direction: column;
    gap: 30px;
}
.ProjectDuration{
    font-size: 13px;
}
.ProjectDetailContainer{
    margin-top: 15px;
    padding-left: 50px;
    border-left: 1px solid rgba(105, 105, 105, 0.281);
}

/* Footer */
.MainFooterContainer{
    margin-top: 50px;
    @include size("both", 100%, 200px);

    display: flex;
    justify-content: center;
    align-items: center;
    color: $footerColor;
    @include bgc(#000);
}
 /* section TItle */
 .SectionTitle{
     @include size("width", 100%, max-content);
     display: flex;
    align-items: center;
}
.SectionTitleIcon{
    font-size: 35px;
}
.SectionLabel{
    padding: 0 20px;
    font-size: 20px;
}
 .lineLeft{
     height: 1px;
     border-bottom: 1px solid #000;
     @include size("width", 0%, 0);
     transition: ease-in-out 0.5s;
 }
.lineRight{
    @include size("both", 100%, 1px);
     border-bottom: 1px solid #000;

 }

 @media (max-width: 768px){ 
     .IntroContainer{
         display: flex;
         align-items: center;
     }
     .IntroTop{
        display: flex;
        flex-direction: column;
        @include size("width", 95%, 0);

        
    }
    .IntroDesc{
        text-align: justify;
    }
     .SkillContainer{
         @include size("both",95%, max-content);

        display: flex;
        flex-direction: column;
        gap: 20px;
        margin-top: 50px;
    }
    .SkillSetContainer{
        text-align: justify;
    }
    .ProjectListContainer{
        margin-top: 50px;
        @include size("width", 95%, 0);
        text-align: left;
        display: flex;
        flex-direction: column;
        gap: 30px;
    }
    .ProjectDetailContainer{
        text-align: justify;
    }
    .NavBarMainContainer{
        @include size("height", 0 , 70px);
    }
    .HamburgerButtonContainer{
        position: absolute;
        top: 10px;
        right: 10px;
        @include size("both", 50px, 50px);
        border: 1px solid rgb(155, 155, 155);
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        display: flex;
    }
    .HamButton{
        font-size: 25px;
        
    }
    .NavBarContainer{
        position: fixed;
        top: 70px;
        left: 100%;
        bottom: 0;
        @include size("width", 100%, 0);
        flex-direction: column;
        background-color: #fff;
        justify-content: flex-start;
        padding-top: 30px;
        transition: ease-in-out 0.2s;
    }
    .NavBarContainer.showMenu{
        left: 0;
        
    }
    .NavBarLink{
        font-size: 25px;
        font-weight: 100;
    }
 }